<template>
<div class="hello">
    <b-container fluid class="bv-example-row">
        <b-row class="text-center headerSearch" align-v="center">
            <b-col class="brandNmae">{{brandName}}</b-col>
            <b-col cols="5" style="postion:relative">
                <b-form-input v-model="searchtext" placeholder="Search Product" v-on:keyup.enter="productsearch">
                </b-form-input>
                <img src="../assets/searchicon.png" class="search-icon" alt="Responsive image" v-on:click="productsearch" />
            </b-col>
            <b-col cols="5">
            </b-col>
        </b-row>
        <b-row class="text-center" style="background-color:white;font-size: 17px;font-weight: bold; height:56px;" align-v="center" v-show="!isLoading">
            <b-col class="text-left">
                <!-- <span>FILTERS</span><span>CLEAR ALL</span> -->
                <b-row>
                    <b-col lg="6">
                        <span>FILTERS</span>
                    </b-col>
                    <b-col lg="6">
                        <span v-on:click="greet" v-show="isClearAll">CLEAR ALL</span>
                    </b-col>
                </b-row>
            </b-col>
            <b-col cols="10" class="text-right">
                <div>
                    <b-form-select v-model="selectedsort" :options="sortoptions" @input="commoncheck(sortoptions)" style="width:300px">
                    </b-form-select>
                </div>
            </b-col>
        </b-row>
        <b-row class="text-center not-found" align-v="center" v-show="isLoading">
            <b-col class="text-center">
                <p>You searched for <span class="not-found-span">{{searchtext}}</span></p>
                <div>
                    <div class="title is-4 has-text-centered not-found-div"> We couldn't find any matches! </div>
                </div>
            </b-col>
        </b-row>
        <b-row class="text-left border-top" align-v="top" style="background-color: white;background-color:white;" v-show="!isLoading">
            <b-col style="background-color:white;padding: 0;" class="border-right">
                <div style=" ">
                    <b-form-group class="label_brand border-bottom">
                        <div class="col-form-label">BRANDS</div>
                        <b-form-checkbox v-for="option in productitem.brands" v-model="selectedbrand" @input="commoncheck(option)" :value="option" :key="option" name="flavour-3a">
                            {{ option | capitalize }}
                        </b-form-checkbox>
                    </b-form-group>
                    <b-form-group class="label_brand border-bottom">
                        <div class="col-form-label">COLORS</div>
                        <b-form-checkbox v-for="option in productitem.colors" v-model="selectedcolor" :value="option" @input="commoncheck(option)" :key="option" name="flavour-3aa">
                            {{ option }}
                        </b-form-checkbox>
                    </b-form-group>

                    <b-form-group class="label_brand border-bottom">
                        <div class="col-form-label">SIZE</div>
                        <b-form-checkbox v-for="option in productitem.sizes" v-model="selectedsize" @input="commoncheck(option)" :value="option" :key="option" name="flavour-3a">
                            {{ option }}
                        </b-form-checkbox>
                        <div></div>
                    </b-form-group>

                </div>
            </b-col>
            <b-col cols="10" style="heigh:200px">
                <b-row class="text-left total-item" align-v="center">
                    <b-col class="text-left">
                        <span style="font-size: 18px;font-weight: bold;">Showing</span><span style="font-size: 18px;color: gray;"> - {{productitem.total}} Products </span>
                    </b-col>
                </b-row>
                <ProductItem :products="productitem" />
            </b-col>
        </b-row>

    </b-container>
