<script>
import axios from "axios";
import ProductItem from './Product';
import store from '../store/store.js'

export default {
    props: {
        msg: String
    },
    components: {
        ProductItem
    },
    computed: {
        productitem() {
            return store.state.brand
        },
        notshow() {
            return store.state.notshow
        },
        isLoading() {
            return store.state.isLoading
        },
        isClearAll() {
            return store.state.isClearAll
        }
    },
    mounted: function () {
        // this.checkboxToggle() //method1 will execute at pageload
    },
    data() {
        return {
            textBrand: 'http://koolkart-ahold-delhaize-team.inmbzp8022.in.dst.ibm.com/api/v1/products?brand=',
            brandName: "koolkart",
            urlQuery: '',
            item: [],
            selectedbrand: [], // Must be an array reference!
            selectedcolor: [], // Must be an array reference!
            selectedsize: [], // Must be an array reference!
            options: [],
            selecteddrop: [],
            sizes: [],
            selectedsort: null,
            sortoptions: [{
                    value: null,
                    text: 'Please select an option'
                },
                {
                    value: 'l2h',
                    text: 'Price: Low to High'
                },
                {
                    value: 'h2l',
                    text: 'Price: High to Low'
                }
            ],
            searchtext: ''
        }
    },
    methods: {
        productsearch: function () {
            //  let payload = 'http://koolkart-ahold-delhaize-team.inmbzp8022.in.dst.ibm.com/api/v1/products?q=' + this.searchtext;
            // store.dispatch('GET_PRODUCT', payload)
           // alert('h');
            const recognition = new window.webkitSpeechRecognition;
            recognition.onresult = (event) => {
                alert(event);
               const speechToText = event.results[0][0].transcript;
                let payload = 'http://koolkart-ahold-delhaize-team.inmbzp8022.in.dst.ibm.com/api/v1/products?q=' + speechToText;
               store.dispatch('GET_PRODUCT', payload)
               alert(payload)
            }
            recognition.start();
            
        },
        greet() {
            store.commit('IS_CLEAR_ALL', false);
            this.selectedbrand = [];
            this.selectedcolor = [];
            this.selectedsize = [];
            // alert('clear all');
        },

        dropselect: function () {
            let lturl = 'http://koolkart-ahold-delhaize-team.inmbzp8022.in.dst.ibm.com/api/v1/products?sortItem=' + this.selectedsort;
            // alert(this.selectedbrand.length);
            //alert(lturl);

            axios.get(lturl)
                .then(function (response) {
                    store.dispatch('SET_BRAND', {
                        data: response.data
                    })
                }).catch(function (error) {
                    // handle error
                    alert(error);
                })
        },

        commoncheck: function () {
            //alert()
            let lturl = 'http://koolkart-ahold-delhaize-team.inmbzp8022.in.dst.ibm.com/api/v1/products?sortItem=' + this.selectedsort;
            if (this.selectedbrand.length != 0 || this.selectedsize.length != 0 || this.selectedcolor.length != 0) {
                store.commit('IS_CLEAR_ALL', true);
            } else {
                // this.selectedbrand = [];
                store.commit('IS_CLEAR_ALL', false);
            }
            if (this.selectedbrand.length > 0) {
                for (var j = 0; j < this.selectedbrand.length; j++) {

                    // if (j == 0) {
                    //     lturl = lturl + '?brand=' + this.selectedbrand[j]
                    // } else {
                    //     lturl = lturl + '&brand=' + this.selectedbrand[j]
                    // }
                    lturl = lturl + '&brand=' + this.selectedbrand[j]
                }
            }

            if (this.selectedsize.length > 0) {
                for (var i = 0; i < this.selectedsize.length; i++) {

                    lturl = lturl + '&size=' + this.selectedsize[i]
                }
            }

            if (this.selectedcolor.length > 0) {
                for (var k = 0; k < this.selectedcolor.length; k++) {

                    lturl = lturl + '&color=' + this.selectedcolor[k]
                }
            }
            axios.get(lturl)
                .then(function (response) {
                    store.dispatch('SET_BRAND', {
                        data: response.data
                    })
                }).catch(function (error) {
                    // handle error
                    alert(error);
                })
        }
    },
    created() {
        //http://9.102.27.20:8080/api/v1/products?brand=Gildan
        axios.get('http://koolkart-ahold-delhaize-team.inmbzp8022.in.dst.ibm.com/api/v1/products')
            .then(function (response) {
                // handle success
                store.dispatch('SET_BRAND', {
                    data: response.data
                })
                this.notshow = true;
            })
    }
}
</script>

